/* ::-webkit-scrollbar {
  display: none;
} */
/* width */
::-webkit-scrollbar {
  width: 10px;
  height: 10px;
  border-radius: 12px;

}

/* Track */
::-webkit-scrollbar-track {
  /* background: #f1f1f1; */
  background: var(--bg_white);
  border-radius: 10px;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #888;
  border-radius: 10px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555;
}

