  padding: 15px 15px 15px 15px;
  background: var(--bg_EDF0F4);
} */

html {
  font-size: var(--htmlFontSize)
}

body {
  font-family: var(--crm-font-regular);
  color: var(--baseColor);
  background-color: var(--bg_EDF0F4);
  margin: 0;
  /* padding: 0px 30px 16px; */
  /* padding: 15px; */
  text-rendering: optimizeLegibility;
  font-size: var(--crm-base-font-size)
}

form,
input,
pre,
select,
textarea {
  margin: 0;
  padding: 0;
  font-size: var(--crm-base-font-size);
  font-variant-ligatures: no-contextual
}

img {
  border: 0
}

a {
  /* color: var(--linkColor); */
  color: var(--primaryColor);
  outline: 0;
  text-decoration: none
}

.error-text {
  display: flex;
  padding-left: 35%;
  padding-top: 5px;
  color: var(--oulineredBorder);

}

.error-field-border {
  border: 1px solid var(--oulineredBorder) !important;
  box-shadow: 0 0 6px var(--oulineredBorder) !important;


}

.error-field-border:focus {
  border: 1px solid var(--oulineredBorder) !important;
  box-shadow: 0 0 6px var(--oulineredBorder) !important;


}

.error-field-border:hover {
  border: 1px solid var(--oulineredBorder) !important;
  box-shadow: 0 0 6px var(--oulineredBorder) !important;


}

.crmSvgIcons {
  display: inline-block;
  background-image: var(--crmLyteSprite)
}

