.lyteProgressBar {
  position: relative
}

.lyteProgressBar.lyteHorizontal {
  height: 14px;
  width: calc(100% - 200px);
  background: #e5e5e5;
  border-radius: 8px
}

.lyteProgressBar .lyteProgressStatus {
  display: block;
  height: 100%;
  border-radius: 20px;
  position: relative;
  overflow: hidden;
  background: #51a2ee;
  color: #fff;
  transition: width .25s
}

.lyteProgressBar .lyteProgressStatus .progressMovingObj,
.lyteProgressBar .lyteProgressStatusStack .progressMovingObj {
  left: 0;
  width: 100%;
  height: 100%;
  display: inline-block;
  position: absolute;
  top: 0;
  box-sizing: border-box;
  background-image: linear-gradient(-45deg, rgba(221, 221, 221, .29) 25%, transparent 25%, transparent 50%, rgba(221, 221, 221, .29) 50%, rgba(221, 221, 221, .29) 75%, transparent 75%, transparent);
  background-size: 20px 20px;
  animation: 1s linear infinite move;
  border-radius: 20px
}

@keyframes move {
  0% {
    background-position: 0 0
  }

  100% {
    background-position: 20px 20px
  }
}

.lyteProgressBar .lyteProgressPercentage {
  font-family: var(--crm-font-regular);
  font-size: 12px;
  line-height: 14px;
  color: #fff;
  left: 50%;
  transform: translateX(-50%);
  position: absolute;
  top: 0;
  z-index: 10
}

.lyteProgressBar.lyteProgressCompleted .lyteProgressStatus {
  background: #3fbd5f
}

.lyteProgressBar.lyteCircle {
  width: 100px;
  height: 100px
}

.lyteProgressBar .lyteCircleType {
  position: relative;
  display: inline-block
}

.lyteCircleType svg {
  font-family: var(--crm-font-regular);
  font-size: 17px;
  fill: #333
}

.lyteProgressBar.lyteCircle .svgValueEle {
  transform: rotate(-90deg)
}

.lyteProgressBar.lyteCircle circle:first-child {
  stroke: #DCE0E3
}

.lyteProgressBar.lyteCircle circle {
  transition: stroke-dashoffset .25s linear
}

.lyteIndeterminate {
  overflow: hidden
}

.inc {
  animation: 2s infinite increase
}

.dec {
  animation: 2s .5s infinite decrease
}

@keyframes increase {
  from {
    left: -5%;
    width: 5%
  }

  to {
    left: 130%;
    width: 100%
  }
}

@keyframes decrease {
  from {
    left: -80%;
    width: 80%
  }

  to {
    left: 110%;
    width: 10%
  }
}

.lyteProgressStatusStack,
