/* HTML: <div class="loader"></div> */
.loader-pulse1 {
  width: 20px;
  aspect-ratio: 1;
  border-radius: 50%;
  background: #000;
  box-shadow: 0 0 0 0 #0004;
  animation: l2 1.5s infinite linear;
  position: relative;
}

.loader-pulse1:before,
.loader-pulse1:after {
  content: "";
  position: absolute;
  inset: 0;
  border-radius: inherit;
  box-shadow: 0 0 0 0 #0004;
  animation: inherit;
  animation-delay: -0.5s;
}

.loader-pulse1:after {
  animation-delay: -1s;
}

@keyframes l2 {
  100% {
    box-shadow: 0 0 0 40px #0000
  }
}

/* HTML: <div class="loader"></div> */
.loader-pulse {
  width: 5px;
  aspect-ratio: 1;
  border-radius: 50%;
  background: darkred;
  box-shadow: 0 0 0 0 #0004;
  animation: l1 1s infinite;
  margin-top: 0.3rem;
  margin-left: 0.4rem;
}

@keyframes l1 {
  100% {
    box-shadow: 0 0 0 10px #0000;
    border-radius: 50%;
  }
}

